import React, { Component } from 'react'
import PropTypes from 'prop-types'
import {
  ButtonSelector,
  SectionContainer,
  Column,
} from './styledComponents';
import Iframe from 'react-iframe'
import { APP_CONFIG } from '../../utils/constants'
import { PageSizer, H4, H3 } from '../styledComponents';

export class MapSelector extends Component {
  static propTypes = {
    offices: PropTypes.arrayOf(PropTypes.object).isRequired,
    markerIcon: PropTypes.object,
    title: PropTypes.string.isRequired,
  }

  state = {
    selectedOffice: 'none'
  };

  componentDidMount = () => {
    const { offices } = this.props;
    this.setState({ selectedOffice: offices[0].id }, () => this.forceUpdate());
  }

  renderSelectors = () => {
    const { offices } = this.props;
    const { selectedOffice } = this.state;
    return offices.map((office) => {
      return (
        <ButtonSelector
          key={office.id}
          className={selectedOffice === office.id ? 'active' : null}
          onClick={() => this.setState({ selectedOffice: office.id })}
        >
          <H4>{office.title}</H4>
          <Column>
            <span>{office.address1}</span>
            <span>{office.address2}. {office.state}</span>
            <span>{office.phone}</span>
          </Column>
        </ButtonSelector>
      );
    });
  };

  render() {
    // const { selectedOffice } = this.state;
    const { title } = this.props;
    // const office = this.props.offices.find((office) => office.id === selectedOffice);
    return (
      <SectionContainer>
        {title && <H3 className="section-heading">{title}</H3>}
        <PageSizer className={'buttons-container'}>
          { this.renderSelectors() }
        </PageSizer>
        <div className="map-container">
          <Iframe
            url={`https://www.google.com/maps/embed/v1/place?key=${APP_CONFIG.embedGoogleApiKey}&q=Sancrisoft+Calle+49+Sur+Envigado+Antioquia`}
            width="100%"
            height="100%"
            id="map"
            className="myClassname"
            display="initial"
            position="relative"
            allowfullscreen
          />
        </div>
      </SectionContainer>
    )
  }
}

export default MapSelector
